.movie{position:relative}.movie_icon-fav{position:absolute;top:1rem;left:1rem;background-image:url("./../../../assets/heart.png");filter:drop-shadow(0 2px rgba(255, 255, 255, 0.746));background-size:cover;width:2.35rem;height:2.1rem;background-repeat:no-repeat;cursor:pointer}.movie_icon-fav.active{background-image:url("./../../../assets/heartPink.png")}.movie__image{overflow:hidden}.movie__image img{width:100%;height:auto;margin-bottom:2.5rem;cursor:pointer}.movie__image:hover .dots{display:block;cursor:pointer}.movie__description{display:flex;flex-direction:column;width:100%}.movie__title{display:flex;justify-content:space-between;transition:.3s}.movie__title:hover{opacity:.7}.movie__genre,.movie__country{text-transform:capitalize;margin-top:1rem;font-size:1.4rem;opacity:.5}.movie__date{font-size:1.4rem;width:6.6rem;height:2.6rem;opacity:.7;border:1px solid #555;border-radius:4px;display:flex;align-items:center;justify-content:center}.movie a{text-decoration:none;color:inherit}.dots{display:none;position:absolute;top:1.5rem;right:1.5rem;width:3.6rem;height:3.6rem;background-color:#232323;border-radius:50%;transition:.3s}.dots::before{content:"•••";position:absolute;top:50%;left:50%;transform:translate(-42%, -50%) rotate(90deg);cursor:pointer}.options__modal{position:absolute;top:.6rem;right:.7rem;width:19rem;background-color:#232323;padding-bottom:1rem;border-radius:4px;display:flex;flex-direction:column;font-size:1.6rem;cursor:default}.options-close{align-self:flex-end;width:1.5rem;height:1.5rem;opacity:.8;margin:.9rem .5rem;cursor:pointer}.options-edit{opacity:.8;padding:.8rem;padding-left:2.3rem;cursor:pointer}.options-edit:hover{background-color:#f65261}.options-delete{opacity:.8;padding-left:2.3rem;height:3.4rem;padding-top:.8rem;cursor:pointer}.options-delete:hover{background-color:#f65261}[data-tool]::after{content:attr(data-tool);position:absolute;width:10rem;left:0;top:0;font-size:1.6rem;background:#424242;font-weight:normal;font-style:normal;color:#fff;padding:.5em;border-radius:4px;pointer-events:none;opacity:0;transition:.5s}[data-tool]:hover::after{opacity:1;top:1.5rem}